* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family: 'PT Sans', sans-serif;
    color: #fff; */
  }

  html, body {
    overflow-x: hidden;
  }
  /* body {
    position: relative
  } */

  .list {
    margin: 0 0 0 8em;
  }

  h2 {
    word-wrap: break-word;
    font-weight: 400;
    line-height: 1.2;
    font-family: 'Playfair Display', 'Helvetica Neue', 'Helvetica',
    'Arial', sans-serif;
    margin: 1em 0em 0.5em 2em;
    letter-spacing: 1px;
    font-size: 38px;
  }

  h3 {
    font-size: 25px;
    font-family: 'Playfair Display', 'Helvetica Neue', 'Helvetica',
    'Arial', sans-serif;
    color: #fff;
  }

  .services, .career, .about, .contact > p {
    margin:0 1em 1em 2em;
    font-size: 18px;
    font-family: 'Playfair Display', 'Helvetica Neue', 'Helvetica',
    'Arial', sans-serif;
  }
  
  .home {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    font-size: 3rem;
  }

  .services, .career, .about, .contact {
    display: flex;
    height: 90vh;
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
}

@media screen and (max-width: 960px) {
  p {
    margin:0 1em 1em 1em;
    font-size: 16px;
    font-family: 'Playfair Display', 'Helvetica Neue', 'Helvetica',
    'Arial', sans-serif;
  }

  .list {
    margin: 0 0 0 4em;
  }
  }
  
